<template>
  <v-container fluid class="down-top-padding">
    <v-row>
      <v-col cols="12" class="text-right">
        <v-btn color="primary" @click="$router.push('/pages/insert-parent')">Yeni Öğrenci Ekle</v-btn>
      </v-col>
        <v-col cols="12" sm="12">
            <v-card>
    <v-card-title>
      <v-text-field
        v-model="search"
        append-icon="mdi-search"
        hint="Öğrenci Adı veya Soyadı, Veli Adı,Soyadı veya Telefon numarası arayabilirsiniz."
        label="Search"
       hide-default-footer
      ></v-text-field>
    </v-card-title>
    <v-data-table
    :loading="loadingData"
      :headers="headers"
      :items="student"
      :search="search"
    >
     <template v-slot:header="{ props: { headers } }">
        <thead>
          <tr>
            <th :colspan="headers.length">
               <p class="text-md-right text-left">
                  <span class="d-block d-md-inline">
                    <v-icon
          color="primary"
          dark
          medium
        >mdi-pencil-box-multiple</v-icon> Öğrenci ve Veliyi Düzenle
                  </span>
                  <span  class="d-block d-md-inline">
                    <v-icon
          color="primary"
          dark
          medium
        >mdi-close-box</v-icon> Öğrenci ve veliyi Kaldır
                  </span>
         <span  class="d-block d-md-inline">
             <v-icon
          color="success"
          dark
          medium

        >mdi-checkbox-marked</v-icon> Veliyi sistemde tekrar aktif et
         </span>

               </p>
            </th>
          </tr>
        </thead>
      </template>
    <template v-slot:item.edit="{ item }">
        <v-tooltip bottom>
      <template v-slot:activator="{ on, attrs }">
        <span @click="setEditUser(item,'student')">
          <v-icon
          color="primary"
          dark
          medium
          v-bind="attrs"
          v-on="on"
        >mdi-pencil-box-multiple</v-icon>
        </span>
      </template>
      <span>Öğrenciyi Düzenle</span>
        </v-tooltip>
         <v-tooltip bottom>
      <template v-slot:activator="{ on, attrs }">
        <span @click="textConfirm = `${item.name} ${item.lastname} isimli öğrenciyi sistemden kaldırıyorsunuz. Emin Misiniz?`;processData={...item,type:'student'};dialog3=true">
          <v-icon
          color="primary"
          dark
          medium
          v-bind="attrs"
          v-on="on"
        >mdi-close-box</v-icon>
        </span>
      </template>
      <span>Öğrenciyi Kaldır</span>
        </v-tooltip>
    </template>
    <template v-slot:item.editParent="{ item }">
        <v-tooltip bottom>
      <template v-slot:activator="{ on, attrs }">
        <span @click="setEditUser(item,'user')">
          <v-icon
          color="primary"
          dark
          medium
          v-bind="attrs"
          v-on="on"
        >mdi-pencil-box-multiple</v-icon>
        </span>
      </template>
      <span>Veliyi Düzenle</span>
        </v-tooltip>
        <v-tooltip bottom>
      <template v-slot:activator="{ on, attrs }">
        <span @click="textConfirm = `${item.parentActive==1?`${item.parentName} ${item.parentLastname} isimli veliyi sistemden kaldırıyorsunuz. Veliyi kaldırdığınız taktirde ${item.name} ${item.lastname} isimli öğrenciyi de kaldırmış olacaksınız. Emin Misiniz?`:`${item.parentName} ${item.parentLastname} isimli veliyi tekrardan aktif etmek üzeresiniz. Emin misiniz? `}`;processData={...item,type:'user'};dialog3=true">
          <v-icon
          :color='item.parentActive==1?"primary":"success"'
          dark
          medium
          v-bind="attrs"
          v-on="on"
        >{{item.parentActive==1?'mdi-close-box':'mdi-checkbox-marked'}}</v-icon>
        </span>
      </template>
      <span>{{item.parentActive==1?'Veliyi ve Öğrenciyi Kaldır':'Veli sistemde aktif olarak bulunmuyor. Veliyi Aktif hale getir.'}}</span>
        </v-tooltip>
    </template>
    </v-data-table>
    <v-pagination v-model="page" :length="pageCount"></v-pagination>
  </v-card>
      </v-col>
    </v-row>
  <loading-dialog :dialogLoading="dialogLoading">{{type}} Detayı Yükleniyor</loading-dialog>
  <edit-user-dialog @setPutUser="setEditUserData=$event" :index="editIndex" :user="editUser" @dialog="editDialog=$event;snackbar=true" :dialog="editDialog" />
  <edit-student-dialog @setPutStudent="setEditStudent=$event" :index="editIndex" :teachers="teachers" :user="editUser" @dialog="editStudentDialog=$event;snackbar=true" :dialog="editStudentDialog" />
  <confirm :text="textConfirm" :dialog="dialog3" @dialog="dialog3=$event" @cancel="dialog3=false" @confirm="deleteItem" />
      <snack-bar :snackbar="snackbar"  @finished="snackbar=$event"></snack-bar>
  </v-container>
</template>

<script>
import loadingDialog from '../../components/dialogs/loadingDialogs'
import editUserDialog from '../../components/dialogs/editUserDialogs'
import editStudentDialog from '../../components/dialogs/editStudentDialogs'
export default {
components:{
  loadingDialog,
  editUserDialog,
  editStudentDialog
},
  name: "Alerts",
watch:{
  setEditStudent(data){
    if(data!=null){
      this.student[data.index].name = data.user.name
      this.student[data.index].lastname = data.user.lastname
      this.student[data.index].birthday = data.user.birthday
      this.student[data.index].isBoy = data.user.isBoy
      this.student[data.index].teacherId = data.user.teacherId
     if(data.image!=''){
        this.student[data.index].image = data.image
     }
      this.setEditStudent=null
    }
  },
  setEditUserData(data){
    if(data!=null){
      this.student[data.index].parentName = data.user.name
      this.student[data.index].parentLastname = data.user.lastName
      this.student[data.index].phone = data.user.phone
      this.setEditUserData=null
    }
  },
    page(data){
 this.initStudents(data)
    },
    search(data){
        if(data.length>3){
             this.loadingData=true
          this.$api.get('i/j/w/student/user?searchFields=student.name,student.lastname,t2.name,t2.lastName,t2.phone&search='+data+'&on=student.parentId=t2.id&fields=student.*,t2.name AS parentName,t2.lastname AS parentLastname,t2.phone,t2.isActive AS parentActive,t2.address&limit=10&offset=0').then(res=>{
          if(res.count!=0){
              this.loading = false
              res.body.forEach(result=>{
                if(result.parentActive==0){
                result.alert = true
              }
              else{
                result.alert = false
              }
              })
              this.student = res.body
              this.pageCount = res.count
                this.loadingData=false
          }
          else{
              this.pageCount = this.page
                this.loadingData=false
          }
      })
        }
        else if(data.length==0){
            this.initStudents(1)
        }
    }
},
  data: () => ({
     dialogLoading:false,
     dialog3:false,
     editIndex:-1,
     snackbar:false,
     textConfirm:"",
     editDialog:false,
     processData:{},
     editStudentDialog:false,
      loading:true,
      setEditStudent:null,
      setEditUserData:null,
      type:"Öğrenci",
      page:1,
      loadingData:true,
      pageCount:0,
      student:[],
      teachers:[],
      headers: [
          {
            text: "Öğrenci Adı",
            align: 'start',
            filterable: true,
            value: 'name',
          },
          { text: 'Soyadı', value: 'lastname' },
          { text: 'Doğum Tarihi', value: 'birthday' },
           { text: 'Düzenle', value: 'edit' },
          { text: 'Veli Telefon', value: 'phone' , filterable: true,},
          { text: 'Veli Adı', value: 'parentName' },
          { text: 'Veli Soyadı', value: 'parentLastname' },
          { text: 'Düzenle', value: 'editParent' },
        ],
        search:"",
        editUser:{}
  }),
  methods:{
   async  deleteItem(){
      let pos = this.student.map(function(e) { return e.id; }).indexOf(this.processData.id);
       if(this.processData.parentActive==1){
      if(this.processData.type=='student'){
        let student = await this.$api.put('student',this.processData.id,{isActive:0})
        if(student.result==1){
        this.snackbar=true
        this.student.splice(pos,1)
        this.dialog3 = false
			this.processData = null
         setTimeout(() => {
           this.snackbar=false
         }, 4000);
      }
      else{
            this.processData = null

      }
      }
      else{
        let parent = await this.$api.put('user',this.processData.parentId,{isActive:0})
        if(parent.result==1){
          let student = await this.$api.put('student',this.processData.id,{isActive:0})
        if(student.result==1){
        this.snackbar=true
        this.student.splice(pos,1)
        this.dialog3 = false
			this.processData = null
         setTimeout(() => {
           this.snackbar=false
         }, 4000);
        }
      }
    }
       }
       else{
        let parent = await this.$api.put('user',this.processData.parentId,{isActive:1})
        if(parent.result==1){
          this.student[pos].parentActive=1
          this.snackbar=true
        this.dialog3 = false
			this.processData = null
         setTimeout(() => {
           this.snackbar=false
         }, 4000);
        }

       }
  },
    setEditUser(row,type){

        if(type=='user'){
          let pos = this.student.map(function(e) { return e.parentId; }).indexOf(row.parentId);
           this.type = "Veli"
           this.editUser = {
             name : row.parentName,
             lastName : row.parentLastname,
             id:row.parentId,
             username : row.username,
             phone:row.phone,
             isTeacher:0,
             address:row.address,
          //   index:pos
           }
              this.editIndex=pos
        }

        else if(type == 'student'){
          let pos = this.student.map(function(e) { return e.id; }).indexOf(row.id);
           this.type = "Öğrenci"
           this.editUser = {
             name : row.name,
             id:row.id,
             lastname: row.lastname,
             isBoy : row.isBoy,
             birthday: row.birthday,
             teacherId : row.teacherId,
             image:row.image,
           //  index:pos
           }
           this.editIndex=pos
        }

      this.dialogLoading = true
      setTimeout(() => {
        this.dialogLoading = false
        if(type=='user')

          this.editDialog = true
        else if(type == 'student')
          this.editStudentDialog = true
      }, 1000);
    },
      initStudents(data){
          this.loadingData=true
          this.$api.get('i/j/w/student/user?on=student.parentId=t2.id&fields=student.*,t2.name AS parentName,t2.lastname AS parentLastname,t2.phone,t2.username,t2.isActive AS parentActive,t2.address&limit=10&offset='+(10*(data-1))).then(res=>{
          if(res.count!=0){
              this.loading = false
              res.body.forEach(result=>{
                result.image = result.image.replace('public/',this.$api.BASE_API_URL)

              if(result.parentActive==0){
                result.alert = true
              }
              else{
                result.alert = false
              }
             result.birthday = result.birthday.substring(0,10)
              })
              this.student = res.body
              this.pageCount = res.count
              this.loadingData=false
          }
          else{
              this.pageCount = this.page
                this.loadingData=false
          }
      })
      },
      initTeachers(){
        this.$api.get('user?filter=isTeacher=1&limit=100').then(res=>{
          if(res.count!=0){
            res.body.forEach(t=>{
              t.fullName = t.name +' '+ t.lastName
            })
            this.teachers = res.body
          }
          else{
            this.teachers =[]
          }
        })
      }
  },
async  mounted(){
  await  this.initStudents(1)
  await  this.initTeachers()
  }
};
</script>
