<template>
  <v-dialog v-model="dialog" persistent max-width="600px">
       <v-card>
        <v-card-title>
          <span class="headline">Öğrenci Düzenleme Ekranı</span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-row>
              <v-col cols="12" class="text-center">
  <v-tooltip bottom>
      <template v-slot:activator="{ on, attrs }">
        <span @click="newImage=null;imageData=''">
          <v-icon
          color="danger"
          dark
          medium
          v-bind="attrs"
          v-on="on"
        >mdi-close-box-multiple</v-icon>
        </span>
      </template>
      <span>Resimi Kaldır</span>
        </v-tooltip>
  <v-list-item-avatar size="170" color="grey darken-3">
          <v-img
            class="elevation-6"
               :src="imageData!=''?imageData:user.image==0?'https://s3.amazonaws.com/37assets/svn/765-default-avatar.png':user.image"
                alt="Resim Bulunmamaktadır"
             ></v-img>
        </v-list-item-avatar>
    <input type="file" name="" @change="setImage" class="d-none" ref="fileInput" id="">
       <v-tooltip bottom>
      <template v-slot:activator="{ on, attrs }">
        <span @click="$refs['fileInput'].click()" class="ml-3">
          <v-icon
          color="primary"
          dark
          medium
          v-bind="attrs"
          v-on="on"
        >mdi-pencil-box-multiple</v-icon>
        </span>
      </template>
      <span>Düzenle</span>
        </v-tooltip>
              </v-col>
              <v-col cols="12" sm="12" md="12">
                <v-text-field label="Öğrenci Adı*" v-model="user.name" :error-messages="errName"  required></v-text-field>
              </v-col>
              <v-col cols="12" sm="12" md="12">
                <v-text-field  label="Öğrenci Soyadı*"   v-model="user.lastname"  :error-messages="errLastname"  hint="example of helper text only on focus"></v-text-field>
              </v-col>
              <v-col cols="12">
                  <p>Cinsiyet Seçiniz</p>
                   <v-radio-group v-model="user.isBoy" :mandatory="false" row>
      <v-radio label="Erkek" :value="1"></v-radio>
      <v-radio label="Kız" :value="0"></v-radio>
    </v-radio-group> </v-col>
              <v-col cols="12" sm="12" md="12">
               <p class="text-danger">  {{errBirthday}}</p>
                <v-date-picker   locale="tr" v-model="user.birthday"></v-date-picker>
              </v-col>

              <v-col cols="12">
                 <v-select
               v-model="user.teacherId"
            :error-messages="errTeacher"
          :items="teachers"
           item-text="fullName"
          item-value="id"
         :label="$t('generate.selectTeacher')"
        ></v-select>
              </v-col>
            </v-row>
          </v-container>
          <small>*Girilmesi zorunlu alanlar</small>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
           <v-btn  class="ma-2" @click="closeModal()" color="info" dark>{{$t('generate.close')}} </v-btn>
           <v-btn :loading="loading" :disabled="loading" class="ma-2" color="primary" @click="putStudent()" dark>{{$t('generate.submit')}}</v-btn>
        </v-card-actions>
      </v-card>
      <snack-bar :snackbar="snackbar" @finished="snackbar=$event"></snack-bar>
  </v-dialog>
</template>

<script>
export default {
props:{
    dialog:{
        default:false,
        type : Boolean,
        required:true
    },
     user:{
        default:()=>{},
        type : Object,
        required:true
    },
     index:{
        default:-1,
        type : Number,
        required:true
    },
       teachers:{
        default:()=>[],
        type : Array,
        required:true
    }
},
data(){
  return{
    imageData:"",
    loading:false,
    errName:[],
    errLastname:[],
    errBirthday:"",
    errTeacher:[],
    newImage:[],
    snackbar:false,
  }
},
methods:{
  putStudent(){
    this.loading=true
 if(this.user.name==''||this.user.lastname==''||this.user.birthday==''||this.user.teacherId==''||this.user.teacherId==null){
   if(this.user.name==''){
     this.errName.push('Öğrenci adı boş geçilemez.')
   }
   if(this.user.lastname==''){
     this.errLastname.push('Öğrenci soyadı boş geçilemez.')
   }
   if(this.user.birthday==''){
     this.errBirthday = 'Öğrenci doğum tarihi seçilmelidir.'
   }
   if(this.user.teacherId=='' || this.user.teacherId==null){
     this.errTeacher.push('Öğrencinin öğretmeni seçilmelidir.')
   }
 }
 else{
    if(this.newImage.length==0){
     this.$api.put('student',this.user.id,this.user).then(res=>{
       if(res.result==1){
             this.$emit('setPutStudent',{index:this.index,user:this.user,image:this.imageData})
          this.snackbar=true
    this.loading=false
    this.closeModal()

       }
       else{
      this.loading=false
       }
     })

    }
    else{
      let fd = new FormData()
      fd.append('name',this.user.name)
      fd.append('lastname',this.user.lastname)
      fd.append('isBoy',this.user.isBoy)
      fd.append('birthday',this.user.birthday)
      fd.append('teacherId',this.user.teacherId)
      fd.append('image',this.newImage)
      this.$api.putfd('student',this.user.id,fd).then(res=>{
       if(res.status=='success'){
         this.snackbar=true
    this.loading=false
      this.$emit('setPutStudent',{index:this.index,user:this.user,image:this.imageData})
  setTimeout(() => {
      this.closeModal()
  }, 100);

       }
    })
    }
 }
  },
  closeModal(){
    this.$emit('dialog',false);
    this.newImage=[]
   setTimeout(() => {
     this.imageData=""
   }, 250);
   // this.dialog = false
  },
  setImage(ev){
    this.newImage = ev.target.files[0]
    let reader = new FileReader()
    var vm = this;

      reader.onload = e => {
        vm.imageData = e.target.result;
      };
      reader.readAsDataURL(ev.target.files[0]);
  }
}
}
</script>

